  padding: 0.2em 0;\r\n}\r\n\r\n/* ==========================================================================\r\nCustom button styles\r\n========================================================================== */\r\n\r\n.icon-button > .btn {\r\n  background-color: transparent;\r\n  border-color: transparent;\r\n  padding: 0.5rem;\r\n  line-height: 1rem;\r\n  &:hover {\r\n    background-color: transparent;\r\n    border-color: transparent;\r\n  }\r\n  &:focus {\r\n    -webkit-box-shadow: none;\r\n    box-shadow: none;\r\n  }\r\n}\r\n\r\n/* ==========================================================================\r\nGeneric styles\r\n========================================================================== */\r\n\r\n/* Temporary workaround for availity-reactstrap-validation */\r\n.invalid-feedback {\r\n  display: inline;\r\n}\r\n\r\n/* other generic styles */\r\n\r\n.title {\r\n  font-size: 1.25em;\r\n  margin: 1px 10px 1px 10px;\r\n}\r\n\r\n.description {\r\n  font-size: 0.9em;\r\n  margin: 1px 10px 1px 10px;\r\n}\r\n\r\n.shadow {\r\n  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;\r\n  border-radius: 2px;\r\n}\r\n\r\n.error {\r\n  color: white;\r\n  background-color: red;\r\n}\r\n\r\n.break {\r\n  white-space: normal;\r\n  word-break: break-all;\r\n}\r\n\r\n.break-word {\r\n  white-space: normal;\r\n  word-break: keep-all;\r\n}\r\n\r\n.preserve-space {\r\n  white-space: pre-wrap;\r\n}\r\n\r\n/* padding helpers */\r\n\r\n@mixin pad($size, $side) {\r\n  @if $size== '' {\r\n    @if $side== '' {\r\n      .pad {\r\n        padding: 10px !important;\r\n      }\r\n    } @else {\r\n      .pad {\r\n        padding-#{$side}: 10px !important;\r\n      }\r\n    }\r\n  } @else {\r\n    @if $side== '' {\r\n      .pad-#{$size} {\r\n        padding: #{$size}px !important;\r\n      }\r\n    } @else {\r\n      .pad-#{$side}-#{$size} {\r\n        padding-#{$side}: #{$size}px !important;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n@include pad('', '');\r\n@include pad('2', '');\r\n@include pad('3', '');\r\n@include pad('5', '');\r\n@include pad('10', '');\r\n@include pad('20', '');\r\n@include pad('25', '');\r\n@include pad('30', '');\r\n@include pad('50', '');\r\n@include pad('75', '');\r\n@include pad('100', '');\r\n@include pad('4', 'top');\r\n@include pad('5', 'top');\r\n@include pad('10', 'top');\r\n@include pad('20', 'top');\r\n@include pad('25', 'top');\r\n@include pad('30', 'top');\r\n@include pad('50', 'top');\r\n@include pad('75', 'top');\r\n@include pad('100', 'top');\r\n@include pad('4', 'bottom');\r\n@include pad('5', 'bottom');\r\n@include pad('10', 'bottom');\r\n@include pad('20', 'bottom');\r\n@include pad('25', 'bottom');\r\n@include pad('30', 'bottom');\r\n@include pad('50', 'bottom');\r\n@include pad('75', 'bottom');\r\n@include pad('100', 'bottom');\r\n@include pad('5', 'right');\r\n@include pad('10', 'right');\r\n@include pad('20', 'right');\r\n@include pad('25', 'right');\r\n@include pad('30', 'right');\r\n@include pad('50', 'right');\r\n@include pad('75', 'right');\r\n@include pad('100', 'right');\r\n@include pad('5', 'left');\r\n@include pad('10', 'left');\r\n@include pad('20', 'left');\r\n@include pad('25', 'left');\r\n@include pad('30', 'left');\r\n@include pad('50', 'left');\r\n@include pad('75', 'left');\r\n@include pad('100', 'left');\r\n\r\n@mixin no-padding($side) {\r\n  @if $side== 'all' {\r\n    .no-padding {\r\n      padding: 0 !important;\r\n    }\r\n  } @else {\r\n    .no-padding-#{$side} {\r\n      padding-#{$side}: 0 !important;\r\n    }\r\n  }\r\n}\r\n\r\n@include no-padding('left');\r\n@include no-padding('right');\r\n@include no-padding('top');\r\n@include no-padding('bottom');\r\n@include no-padding('all');\r\n\r\n/* end of padding helpers */\r\n\r\n.no-margin {\r\n  margin: 0px;\r\n}\r\n@mixin voffset($size) {\r\n  @if $size== '' {\r\n    .voffset {\r\n      margin-top: 2px !important;\r\n    }\r\n  } @else {\r\n    .voffset-#{$size} {\r\n      margin-top: #{$size}px !import